import initSiteDB from './initSite.json'
import initSettingDB from './initSetting.json'
const db = {
  //  db 
  async getAllDB (db) {
    try {
      const res = uni.getStorageSync(`ZY-${db}-DB`)
      return { flag: true, data: res, msg: ` ${db} ` }
    } catch (err) {
      return { flag: false, data: null, msg: err }
    }
  },
  //  db  key 
  async checkItemKey (db, key) {
    const res = await this.getAllDB(db)
    if (res.data.length > 0) {
      for (const i of res.data) {
        if (i === key) {
          return { flag: false, data: null, msg: `${key} ` }
        }
      }
    }
    return { flag: true, data: null, msg: `${key} ` }
  },
  // db  item key
  async addItemKey (db, key) {
    const res = await this.checkItemKey(db, key)
    if (res.flag) {
      const keys = await this.getAllDB(db)
      let arr = []
      if (keys.data && keys.data.length > 0) {
        arr = [...keys.data]
      }
      arr.push(key)
      try {
        uni.setStorageSync(`ZY-${db}-DB`, arr)
        return { flag: true, data: key, msg: '' }
      } catch(err) {
        return { flag: false, data: key, msg: err }
      }
    } else {
      return res
    }
  },
  // db  item
  async add (db, item) {
    const res = await this.addItemKey(db, item.key)
    if (res && res.flag) {
      try {
        uni.setStorageSync(`${db}-${item.key}`, item)
        return { flag: true, data: {...item}, msg: '' }
      } catch(err) {
        return err
      }
    } else {
      return res
    }
  },
  //  item
  async get (db, key) {
    try {
      const res = uni.getStorageSync(`${db}-${key}`)
      if (res) {
        return { flag: true, data: {...res}, msg: ` ${key} ` }
      }
      return { flag: false, data: null, msg: ` ${key} ` }
    } catch (err) {
      return { flag: false, data: null, msg: {...err} }
    }
  },
  async update (db, item) {
    const res = await this.get(db, item.key)
    if (res && res.flag) {
      try {
        uni.setStorageSync(`${db}-${item.key}`, item)
        return { flag: true, data: {...item}, msg: '' }
      } catch(err) {
        return err
      }
    } else {
      return res
    }
  },
  //  db  item
  async getAll (db) {
    const res = await this.getAllDB(db)
    if (res.data.length > 0) {
      let arr = []
      for (const i of res.data) {
        const data = await this.get(db, i)
        arr.push(data.data)
      }
      return { flag: true, data: arr, msg: '' }
    } else {
      return res
    }
  },
  //  db  item
  async remove (db, key) {
    const res = await this.getAllDB(db)
    if (res.data.length <= 0) {
      return { flag: false, data: null, msg: '' }
    } else {
      let arr = [...res.data]
      const index = arr.indexOf(key)
      if (index >= 0) {
        arr.splice(index, 1)
        try {
          uni.setStorageSync(`ZY-${db}-DB`, arr)
          uni.removeStorageSync(`${db}-${key}`)
          return { flag: true, data: key, msg: `${key} ` }
        } catch(err) {
          return { flag: false, data: key, msg: err }
        }
      } else {
        return { flag: false, data: key, msg: `${key} , ` }
      }
    }
  },
  //  db  item
  async removeAll (db) {
    const res = await this.getAllDB(db)
    if (res.data.length <= 0) {
      return { flag: false, data: null, msg: '' }
    } else {
      let arr = [...res.data]
      for (const i of arr) {
        await this.remove(db, i)
      }
      try {
        uni.removeStorageSync(`ZY-${db}-DB`)
        return { flag: true, data: null, msg: `${db} ` }
      } catch (err) {
        return { flag: false, data: null, msg: `${db} ` }
      }
    }
  },
  //  db 
  async init (db) {
    const res = await this.getAllDB(db)
    if (res.data.length > 0) {
      return { flag: false, data: null, msg: `, ${db} ` }
    } else {
      await this.reset(db)
      return { flag: true, data: null, msg: '' }
    }
  },
  //  db 
  async reset (db) {
    await this.removeAll(db)
    if (db === 'site') {
      for (const i of initSiteDB) {
        await this.add(db, i)
      }
    }
    if (db === 'setting') {
      for (const i of initSettingDB) {
        await this.add(db, i)
      }
    }
    return { flag: true, data: null, msg: `${db} ` }
  },
  // 
  async clearDB () {
    try {
      uni.clearStorageSync()
      return { flag: true, data: null, msg: '' }
    } catch(err) {
      return { flag: false, data: null, msg: err }
    }
  }
}

export default db
